@import url(https://fonts.googleapis.com/css2?family=Source+Sans+Pro:wght@300;400;600;700&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

:root {
  --color-pink:#D61F99;
  --color-aqua:turquoise;
  --dark-background:#120425;
  --color-orange: #FB7508;
  --color-render-title:#F24F16;  
  --font-color-normal: #fff;
  --font-size-title:1.8rem;    
  --font-size-subtitle: 1.5rem;
  --font-size-normal: 1.2rem;
  --font-size-small: 0.9rem;
}

/*************************************************** HEADER **/
.header-links {
  padding-top: 5px;
  display: flex;  
  justify-content: space-around;
}
.header-links a {
  text-decoration: none;
  font-size: var(--font-size-subtitle);
  color: var(--font-color-normal);
  width: 50%;
  text-align: center;
  position: relative;  
}

.header button img {
  width: 100%;
}
.header-links a.active-component{
  color: var(--color-orange);
}
.header-links a.active-component::after{
  content: "";
  position: absolute;
  width:100%;
  height: 2px;
  background: var(--color-orange);
  bottom: 0;
  left: 0;    
  transform: scale(1);
}

/********************************************** BUTTON MODE-BUTTON **/
.mode_button-section {
  display: flex;
  justify-content: flex-end;
  padding-right: 10px;
}
.mode-button {
  margin-top: 15px;
  background: #fff;
  width: 50px;
  height: 25px;
  border-radius: 20px;
  padding: 2px;
}
.mode-button figure {
  margin: 0;
  height: 95%;
  width: 50%;
  border-radius: 100%;
  background: var(--dark-background);
  transition: 0.5s;
}

/************************************************* EFFECTOS PARA EL LIGHT-MODE */
.header.light-mode .header-links a{
  color: var(--dark-background);
}
.header.light-mode .header-links a:hover{
  color: var(--color-orange);
}
.header.light-mode .mode-button figure{
  transform: translateX(100%);
  background: var(--font-color-normal);
}
.header.light-mode .mode-button{
  background: var(--dark-background);  
}


/********************************************** PRIMER BREAK****(TABLET) ****/
@media screen and (min-width:550px) {  
  .header-links{
    max-width:973px;
    margin: auto;
  }
}


/********************************************** SEGUNDO BREAK****(PC) ****/
@media screen and (min-width:1200px) {
  .header{
    display: flex;
    /* justify-content: flex-start; */
    justify-content: space-between;
    padding-right: 20px;
  }
  .mode_button-section{
    align-self: flex-end;
  }
  .mode-button figure{
    cursor: pointer;
  }
  .header-links{
    max-width: none;
    margin: 0;
    width: 40%;
    padding-left: 40px;
    padding-top: 20px;
    /* transition: .5s ease-in; */
  }
  .header-links a{
    position: relative;
    transition: .4s;
  }
  .header-links a:hover{
    color: var(--color-orange);
    transform: scale(.9);
  }

  .header-links a::after{
    content: "";
    position: absolute;
    width:100%;
    height: 2px;
    background: var(--color-orange);
    bottom: 0;
    left: 0;
    transition: .5s;    
    transform: scale(0);
  }
  .header-links a:hover::after{
    transform: scale(1);
  }  
}








body{
  color:var(--font-color-normal);
  font-family: 'Source Sans Pro', sans-serif;
}
.home{    
    background-image: url("https://i.pinimg.com/474x/22/05/c1/2205c1e11254a5de17bfa4630ba55e55.jpg");
    /* background-image: url("https://i.pinimg.com/236x/a2/5d/af/a25daface186cce5bbaf7c6d2a36832c--man-cave-hobby.jpg"); */    
    min-height: 100vh;    
}
/* *********************************************  HOME INTRO */
.home-intro{
    width: 90%;
    margin: auto;
    margin-top: 70px;
    text-align: center;
    font-size: var(--font-size-normal);
    font-weight: 700;
    color: var(--font-color-normal);
    -webkit-text-stroke: 1px #000;    
}
.home-intro p{
    line-height: 1.5;
}

/* *******************************************  HOME-FORM*/
.home-form{
    display: flex;
    flex-direction: column;
    width: 85%;    
    margin: auto;    
    margin-top: 150px;
    max-width:360px;
    padding-bottom: 20px;
}
.home-form input{
    font-size: var(--font-size-small);
    margin-top: 6px;
    outline: none;
    border: 1px solid #0006;      
    background: var(--font-color-normal);
    height: 25px;
}
.home-form input[type="text"]{
    border-radius:10px;    
}
.home-form input[type="submit"]{
    width: 35%;
    margin: auto;
    cursor: pointer;
    margin-top: 12px;
    border-radius: 15px;
    font-size: var(--font-size-small);
    background: var(--color-orange);
}
.home-form input[type="submit"]:hover{
    background: #f90;
    color: var(--font-color-normal);
}



/** *************************************  PRIMER BREAK****(TABLET)  */
@media screen and (min-width:550px) {
    .home-intro{
        font-size: var(--font-size-subtitle);
        margin-bottom: 150px;
        max-width: 627px;
    }
    .home-form input{
        height: 30px;
    }    
}



/** *******************************************SEGUNDO BREAK****(PC) **/
@media screen and (min-width:1200px){
    .home{        
        background-image: url("https://www.santabanta.com/images/wallpapers/babes/black-guitar-musical-instrument-wallpaper-1024-768-0.jpg");
        background-size: cover;
        background-position: center; 
        overflow: hidden;       
    }
    .home-content{
        display: flex;        
        justify-content: space-around;
        align-items: center;
    }    
    .home-intro{
        margin-top: 150px;
        font-size: var(--font-size-title);
    }

    .home-img{
        align-self: center;        
        /* overflow: hidden; */
    }
    .home-img img{        
        width: 450px;        
        animation: imgTraslation 5s infinite;        
    }

    .home-form input{
        margin-top: 16px;
    } 

    @keyframes imgTraslation {
        0%{
            transform: translatey(-200%);            
        }
        20%{
            transform: translateY(10%);            
        }
        40%{
            transform: translateY(-10%);            
        }
        60%{
            transform: translateY(10%);            
        }
        80%{
            transform: translateY(-10%);            
        }
        100%{            
            transform: translateY(300%);
        }
    }
}



.my-list{
    background: var(--dark-background);
    padding-bottom: 15px;
    min-height: 100vh;    
}



.list-cards{
    margin-top: 50px;
    display: grid;
    grid-template-columns: repeat(auto-fill,minmax(290px, 1fr));
    grid-row-gap: 22px;
    row-gap: 22px;    
}
.list-cards a{
    text-decoration: none;
    color: var(--font-color-normal);
}

.card{
    position: relative;
    display: flex;
    justify-content: space-between;
    align-items: center;    
    background: linear-gradient(to right, #ee9ca733, #ffdde133);
    width: 85%;
    max-width: 300px;
    margin: auto;
    padding: 0 15px;
    transition: .1s ease-in;
    cursor: pointer;        
}
/************************************** EFFECTO HOVER(-- TODO EL CARD --) */
.card:hover{
    transform:translate(-2%,-2%);
    box-shadow: 5px 5px 15px 5px rgba(0,0,0,0.73);
}



.card article{
    text-align: center;    
    color: var(--color-render-title);    
}
.card article *{
    margin: 10px 0;
    text-transform: capitalize;    
}

.card img{
    width: 40px; 
    position: relative;
}

/* *************************** MENSAJE AL HACER HOVER SOBRE EL BOTON DE FAV  ****** ****************************** */
.remove_message{
    position: absolute;
    top: 0;
    right: 10px;
}
.remove_message p{
    margin: 0;
    font-size: var(--font-size-small);     
}


/* ********************************************* LIHGHT - MODE */
.my-list.light-mode{
    background: var(--font-color-normal);
    color: var(--dark-background);
}
.my-list.light-mode .card{
    background: linear-gradient(to right, #000428db, #004e92bd); 
    background: linear-gradient(to right, #ee9ca7, #ffdde1);  
}
.my-list.light-mode .card article{
    color: var(--dark-background);
}
.my-list.light-mode .card:hover{    
    box-shadow: 5px 5px 15px 5px rgba(0,0,0,0.43);
}
.my-list.light-mode .list-cards a{    
    color: var(--dark-background);
}



/* ****************************************************BREAK 1200px */
@media screen and (min-width:1200px) {
    .list-cards{
        width: 90%;
        margin: auto;
        margin-top: 50px;
    }
}

